import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import Layout from './components/Layout';
import MainContent from './pages/MainContent';
import Technology from "./pages/Technology";

// To theme vi mu ch o l xanh l cy
const theme = createTheme({
    palette: {
        primary: {
            main: '#4CAF50', // Mu xanh l cy ch o
            dark: '#388E3C', // Mu xanh l cy m hn
            light: '#C8E6C9', // Mu xanh l cy nht hn
        },
        secondary: {
            main: '#8BC34A', // Mu ph (c th l mt tng xanh l cy nht khc)
        },
        background: {
            default: '#F1F8E9', // Mu nn nht
        },
        text: {
            primary: '#1B5E20', // Mu vn bn chnh (mu xanh l cy m)
            secondary: '#4E342E', // Mu vn bn ph (mu nu m hoc en)
        },
    },
    typography: {
        fontFamily: 'Arial, sans-serif', // Chn font ph hp cho ton b trang web
        h1: {
            fontSize: '2rem',
            fontWeight: 700,
            color: '#1B5E20', // Mu xanh l cy m
        },
        h2: {
            fontSize: '1.75rem',
            fontWeight: 600,
            color: '#1B5E20', // Mu xanh l cy m
        },
        body1: {
            fontSize: '1rem',
            color: '#4E342E', // Mu nu m cho ni dung vn bn
        },
    },
});

const App = () => {
    return (
        <ThemeProvider theme={theme}>
            <Router>
                <Routes>
                    <Route path="/" element={<Layout />}>
                        <Route index element={<MainContent />} />
                        <Route path="technology" element={<Technology />} />
                    </Route>
                </Routes>
            </Router>
        </ThemeProvider>
    );
};

export default App;
